<template>
  <div class="chunk-uploader">
    <!-- ‰∏ä‰º†ËøõÂ∫¶ÂØπËØùÊ°Ü -->
    <div class="upload-dialog">
      <div class="upload-dialog-header">
        <h3>Êñá‰ª∂‰∏ä‰º†ËøõÂ∫¶</h3>
        <button 
          type="button" 
          class="toggle-logs-btn" 
          @click="showLogs = !showLogs"
        >
          {{ showLogs ? 'ÈöêËóèÊó•Âøó' : 'ÊòæÁ§∫Êó•Âøó' }}
        </button>
      </div>
      <div class="upload-dialog-body">
        <div v-for="(file, index) in uploadTasks" :key="index" class="file-item">
          <div class="file-info">
            <div class="file-name">{{ file.name }}</div>
            <div class="file-size">{{ formatFileSize(file.size) }}</div>
            <div class="file-status">
              {{ getFileStatus(file) }}
              <span v-if="file.error" class="error-message">: {{ file.error }}</span>
            </div>
          </div>
          <div class="progress-container">
            <div class="progress-bar" :style="{ width: file.progress + '%' }"></div>
            <div class="progress-text">{{ file.progress.toFixed(1) }}%</div>
          </div>
        </div>

        <!-- Êó•ÂøóÂå∫Âüü -->
        <div v-if="showLogs" class="log-container">
          <div class="log-header">
            <h4>‰∏ä‰º†Êó•Âøó</h4>
            <div class="log-actions">
              <button 
                type="button" 
                class="clear-logs-btn" 
                @click="clearLogs"
              >
                Ê∏ÖÁ©∫Êó•Âøó
              </button>
              <button
                type="button"
                class="copy-logs-btn"
                @click="copyLogs"
              >
                Â§çÂà∂Êó•Âøó
              </button>
            </div>
          </div>
          <div class="log-content" ref="logContent">
            <div v-for="(log, index) in logs" :key="index" class="log-entry" :class="getLogClass(log)">
              <span class="log-time">{{ log.time }}</span>
              <span class="log-level">[{{ log.level }}]</span>
              <span class="log-message">{{ log.message }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { initChunk, uploadChunk, completeChunk } from '../api/events';
import SparkMD5 from 'spark-md5';

export default {
  name: 'ChunkUploader',
  props: {
    // ÊúÄÂ§ßÂπ∂Ë°å‰∏ä‰º†Êñá‰ª∂Êï∞
    maxParallelFiles: {
      type: Number,
      default: 3
    },
    // ÊØè‰∏™Êñá‰ª∂ÊúÄÂ§ßÂπ∂Ë°åÂàÜÁâáÊï∞
    maxParallelChunks: {
      type: Number,
      default: 3
    },
    // ÊØè‰∏™ÂàÜÁâáÂ§ßÂ∞è(bytes) - ÈªòËÆ§ 512k
    chunkSize: {
      type: Number,
      default:  512 * 1024
    }
  },
  data() {
    return {
      uploadTasks: [],
      activeUploads: 0,
      isUploading: false,
      showLogs: false,
      logs: [],
      maxLogs: 100  // ÊúÄÂ§ö‰øùÂ≠òÁöÑÊó•ÂøóÊù°Êï∞
    };
  },
  computed: {
    allTasksCompleted() {
      return this.uploadTasks.length > 0 && 
             this.uploadTasks.every(task => task.status === 'completed' || task.status === 'error');
    }
  },
  methods: {
    // Êó•ÂøóÁõ∏ÂÖ≥ÊñπÊ≥ï
    log(level, message) {
      const time = new Date().toLocaleTimeString();
      this.logs.unshift({ time, level, message });
      
      // ÈôêÂà∂Êó•ÂøóÊï∞Èáè
      if (this.logs.length > this.maxLogs) {
        this.logs = this.logs.slice(0, this.maxLogs);
      }
      
      // ÂêåÊó∂ËæìÂá∫Âà∞ÊéßÂà∂Âè∞
      switch (level) {
        case 'ERROR':
          console.error(message);
          break;
        case 'WARN':
          console.warn(message);
          break;
        case 'INFO':
          console.log(message);
          break;
        default:
          console.log(message);
      }
      
      // Â¶ÇÊûúÊó•ÂøóÂå∫ÂüüÂèØËßÅÔºåÊªöÂä®Âà∞ÊúÄÊñ∞Êó•Âøó
      this.$nextTick(() => {
        if (this.showLogs && this.$refs.logContent) {
          this.$refs.logContent.scrollTop = 0;
        }
      });
    },
    
    logInfo(message) {
      this.log('INFO', message);
    },
    
    logWarn(message) {
      this.log('WARN', message);
    },
    
    logError(message) {
      this.log('ERROR', message);
    },
    
    clearLogs() {
      this.logs = [];
    },
    
    copyLogs() {
      // Â∞ÜÊó•ÂøóÊ†ºÂºèÂåñ‰∏∫ÊñáÊú¨
      const logText = this.logs
        .map(log => `${log.time} [${log.level}] ${log.message}`)
        .join('\n');
      
      // Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø
      navigator.clipboard.writeText(logText)
        .then(() => {
          this.logInfo('Êó•ÂøóÂ∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø');
        })
        .catch(err => {
          this.logError('Â§çÂà∂Êó•ÂøóÂ§±Ë¥•: ' + err);
        });
    },
    
    getLogClass(log) {
      return {
        'log-info': log.level === 'INFO',
        'log-warn': log.level === 'WARN',
        'log-error': log.level === 'ERROR'
      };
    },
    
    // Ê∑ªÂä†Âçï‰∏™Êñá‰ª∂Âà∞‰∏ä‰º†ÈòüÂàóÂπ∂Ëá™Âä®ÂºÄÂßã‰∏ä‰º†
    addFile(file) {
      try {
        if (!file) {
          this.logError('Â∞ùËØïÊ∑ªÂä†Êñá‰ª∂Ôºå‰ΩÜÊñá‰ª∂ÂØπË±°‰∏∫Á©∫!');
          return null;
        }
        
        this.logInfo(`Ê∑ªÂä†Êñá‰ª∂Âà∞‰∏ä‰º†ÈòüÂàó: ${file.name}, Â§ßÂ∞è: ${this.formatFileSize(file.size)}`);
        
        // ÂàõÂª∫Êñ∞‰ªªÂä°
        const newTask = {
          file,
          name: file.name,
          size: file.size,
          progress: 0,
          status: 'pending', // pending, calculating, uploading, completed, error
          error: null,
          taskId: null,
          totalChunks: Math.ceil(file.size / this.chunkSize),
          uploadedChunks: 0,
          chunkQueue: [],
          md5: null,
          captureTime: null
        };
        
        // Ê∑ªÂä†Âà∞‰ªªÂä°ÂàóË°®
        this.uploadTasks.push(newTask);
        this.logInfo(`Ê∑ªÂä†ÊàêÂäüÔºåÂΩìÂâç‰ªªÂä°Êï∞: ${this.uploadTasks.length}`);
        
        // Ëá™Âä®ÂºÄÂßãËÆ°ÁÆóËØ•Êñá‰ª∂ÁöÑMD5Âπ∂‰∏ä‰º†
        if (!this.isUploading) {
          this.logInfo('ÂêØÂä®‰∏ä‰º†ÊµÅÁ®ã');
          setTimeout(() => {
            try {
              this.startUpload();
            } catch (error) {
              this.logError(`ÂºÄÂßã‰∏ä‰º†ËøáÁ®ãÂá∫Èîô: ${error.message || error}`);
            }
          }, 100); // Ê∑ªÂä†Â∞èÂª∂ËøüÁ°Æ‰øùDOMÂ∑≤Êõ¥Êñ∞
        } else {
          this.logInfo('Â∑≤Êúâ‰∏ä‰º†‰ªªÂä°ËøõË°å‰∏≠ÔºåÊñ∞‰ªªÂä°Â∞ÜÊéíÈòüÁ≠âÂæÖ');
        }
        
        return newTask;
      } catch (error) {
        this.logError(`Ê∑ªÂä†Êñá‰ª∂ËøáÁ®ã‰∏≠ÂèëÁîüÈîôËØØ: ${error.message || error}`);
        return null;
      }
    },
    
    clearCompletedTasks() {
      // Ê∏ÖÁ©∫Â∑≤ÂÆåÊàêÊàñÈîôËØØÁöÑ‰ªªÂä°
      this.uploadTasks = this.uploadTasks.filter(
        task => task.status !== 'completed' && task.status !== 'error'
      );
    },
    
    formatFileSize(bytes) {
      if (bytes < 1024) return bytes + ' B';
      else if (bytes < 1024 * 1024) return (bytes / 1024).toFixed(2) + ' KB';
      else if (bytes < 1024 * 1024 * 1024) return (bytes / (1024 * 1024)).toFixed(2) + ' MB';
      else return (bytes / (1024 * 1024 * 1024)).toFixed(2) + ' GB';
    },
    
    getFileStatus(file) {
      switch (file.status) {
        case 'pending': return 'Á≠âÂæÖ‰∏ä‰º†';
        case 'calculating': return 'ËÆ°ÁÆóMD5...';
        case 'uploading': return `‰∏ä‰º†‰∏≠ (${file.uploadedChunks}/${file.totalChunks})`;
        case 'verifying': return 'È™åËØÅ‰∏≠...';
        case 'completed': return 'ÂÆåÊàê';
        case 'error': return 'ÈîôËØØ';
        default: return file.status;
      }
    },
    
    async startUpload() {
      if (this.isUploading) {
        this.logInfo('Â∑≤Êúâ‰∏ä‰º†‰ªªÂä°ËøõË°å‰∏≠ÔºåË∑≥ËøáÊú¨Ê¨°Ë∞ÉÁî®');
        return;
      }
      
      this.logInfo(`ÂºÄÂßã‰∏ä‰º†Â§ÑÁêÜ, ÂΩìÂâç‰ªªÂä°Êï∞: ${this.uploadTasks.length}`);
      this.isUploading = true;
      
      try {
        // ËøáÊª§Âá∫ÈúÄË¶Å‰∏ä‰º†ÁöÑ‰ªªÂä°
        const pendingTasks = this.uploadTasks.filter(task => 
          task.status === 'pending' || (task.status === 'error' && !task.taskId)
        );
        
        this.logInfo(`ÂæÖÂ§ÑÁêÜ‰ªªÂä°Êï∞: ${pendingTasks.length}`);
        
        if (pendingTasks.length === 0) {
          this.logInfo('Ê≤°ÊúâÂæÖÂ§ÑÁêÜÁöÑ‰ªªÂä°ÔºåÊ†áËÆ∞‰∏ä‰º†ËøáÁ®ãÁªìÊùü');
          this.isUploading = false;
          // Ê£ÄÊü•ÊòØÂê¶ÈúÄË¶ÅËß¶ÂèëÂÆåÊàê‰∫ã‰ª∂
          this.checkAllTasksCompleted();
          return;
        }
        
        // ÂºÄÂßãËÆ°ÁÆóMD5
        this.logInfo(`ÂºÄÂßãÂ§ÑÁêÜ ${pendingTasks.length} ‰∏™‰ªªÂä°ÁöÑMD5ËÆ°ÁÆó`);
        for (const task of pendingTasks) {
          await this.calculateFileMD5(task);
        }
      } catch (error) {
        this.logError(`‰∏ä‰º†Â§ÑÁêÜËøáÁ®ã‰∏≠ÂèëÁîüÈîôËØØ: ${error.message || error}`);
        this.isUploading = false;
      }
    },
    
    async calculateFileMD5(task) {
      this.logInfo(`ÂºÄÂßãËÆ°ÁÆóMD5ÔºåÊñá‰ª∂: ${task.name}`);
      task.status = 'calculating';
      
      try {
        const md5 = await this.computeFileMD5(task.file);
        this.logInfo(`MD5ËÆ°ÁÆóÂÆåÊàê: ${md5}`);
        task.md5 = md5;
        await this.prepareFileUpload(task);
      } catch (error) {
        this.logError(`MD5ËÆ°ÁÆóÂ§±Ë¥•: ${error.message || error}`);
        task.status = 'error';
        task.error = 'MD5ËÆ°ÁÆóÂ§±Ë¥•';
        this.checkAllTasksCompleted();
      }
    },
    
    async computeFileMD5(file) {
      return new Promise((resolve, reject) => {
        this.logInfo(`ÂºÄÂßãÂàÜÁâáËÆ°ÁÆóMD5: ${file.name}, Â§ßÂ∞è: ${file.size}Â≠óËäÇ`);
        
        // ÂØπ‰∫éÈùûÂ∏∏Â∞èÁöÑÊñá‰ª∂Ôºå‰ΩøÁî®ÁÆÄÂçïÁöÑÁÆóÊ≥ï
        if (file.size < 1024 * 1024) { // Â∞è‰∫é1MBÁöÑÊñá‰ª∂
          this.logInfo('Â∞èÊñá‰ª∂‰ΩøÁî®ÁÆÄÂçïMD5ËÆ°ÁÆó');
          const reader = new FileReader();
          reader.onload = (e) => {
            try {
              const arrayBuffer = e.target.result;
              const spark = new SparkMD5.ArrayBuffer();
              spark.append(arrayBuffer);
              const hash = spark.end();
              resolve(hash);
            } catch (err) {
              reject(err);
            }
          };
          reader.onerror = (err) => reject(err);
          reader.readAsArrayBuffer(file);
          return;
        }
        
        // ÂØπ‰∫éÂ§ßÊñá‰ª∂Ôºå‰ΩøÁî®ÂàÜÁâáËÆ°ÁÆó
        const blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
        const chunkSize = 2097152; // 2MB per chunk for MD5 calculation
        const chunks = Math.ceil(file.size / chunkSize);
        let currentChunk = 0;
        const spark = new SparkMD5.ArrayBuffer();
        const fileReader = new FileReader();
        
        this.logInfo(`Êñá‰ª∂Â∞ÜÂàÜ‰∏∫ ${chunks} ‰∏™ÂùóËÆ°ÁÆó`);
        
        fileReader.onload = (e) => {
          try {
            spark.append(e.target.result); // Append array buffer
            currentChunk++;
            
            const progress = Math.round((currentChunk / chunks) * 100);
            if (currentChunk % 10 === 0 || currentChunk === chunks) {
              this.logInfo(`MD5ËÆ°ÁÆóËøõÂ∫¶: ${progress}%`);
            }
            
            if (currentChunk < chunks) {
              loadNext();
            } else {
              const hash = spark.end();
              this.logInfo(`MD5ËÆ°ÁÆóÂÆåÊàê: ${hash}`);
              resolve(hash); // Return the MD5 hash
            }
          } catch (error) {
            this.logError(`MD5ËÆ°ÁÆó‰∏≠ÂèëÁîüÈîôËØØ: ${error.message || error}`);
            reject(error);
          }
        };
        
        fileReader.onerror = (error) => {
          this.logError(`FileReaderÈîôËØØ: ${error.message || error}`);
          reject(error);
        };
        
        const loadNext = () => {
          try {
            const start = currentChunk * chunkSize;
            const end = Math.min(file.size, start + chunkSize);
            const chunk = blobSlice.call(file, start, end);
            fileReader.readAsArrayBuffer(chunk);
          } catch (error) {
            this.logError(`Âä†ËΩΩ‰∏ã‰∏Ä‰∏™ÂùóÊó∂Âá∫Èîô: ${error.message || error}`);
            reject(error);
          }
        };
        
        loadNext();
      });
    },
    
    async prepareFileUpload(task) {
      try {
        this.logInfo(`ÂáÜÂ§á‰∏ä‰º†Êñá‰ª∂: ${task.name}`);
        
        // ÂáÜÂ§áÂàÜÂùó‰∏ä‰º†ËØ∑Ê±Ç
        const initRequest = {
          fileName: task.name,
          fileType: task.file.type,
          fileSize: task.file.size,
          chunkCount: task.totalChunks,
          fileMD5: task.md5
        };
        
        this.logInfo(`ÂèëÈÄÅÂàùÂßãÂåñËØ∑Ê±Ç: ${JSON.stringify(initRequest)}`);
        
        // ÂàùÂßãÂåñÂàÜÂùó‰∏ä‰º†
        const response = await initChunk(initRequest);
        this.logInfo(`ÂàùÂßãÂåñÂìçÂ∫î: ${JSON.stringify(response)}`);
        
        if (response.success && response.data) {
          task.taskId = response.data.taskId;
          task.status = 'uploading';
          this.logInfo(`Ëé∑Âæó‰ªªÂä°ID: ${task.taskId}, ÂàõÂª∫ÂàÜÁâáÈòüÂàó`);
          
          this.createChunkQueue(task);
          this.processUploadQueue();
        } else {
          this.logError(`ÂàùÂßãÂåñÂ§±Ë¥•: ${JSON.stringify(response)}`);
          task.status = 'error';
          task.error = response.message || 'ÂàùÂßãÂåñ‰∏ä‰º†Â§±Ë¥•';
          this.checkAllTasksCompleted();
        }
      } catch (error) {
        this.logError(`‰∏ä‰º†ÂáÜÂ§áÈîôËØØ: ${error.message || error}`);
        task.status = 'error';
        task.error = 'ÂáÜÂ§á‰∏ä‰º†Â§±Ë¥•';
        this.checkAllTasksCompleted();
      }
    },
    
    createChunkQueue(task) {
      const { file } = task;
      const { chunkSize } = this;
      task.chunkQueue = [];
      
      for (let i = 0; i < task.totalChunks; i++) {
        const start = i * chunkSize;
        const end = Math.min(file.size, start + chunkSize);
        const chunkFile = file.slice(start, end);
        
        task.chunkQueue.push({
          index: i,
          file: chunkFile,
          uploaded: false,
          uploading: false,
          retries: 0
        });
      }
    },
    
    async processUploadQueue() {
      this.logInfo('Â§ÑÁêÜ‰∏ä‰º†ÈòüÂàó');
      
      // ËÆ°ÁÆóÂΩìÂâçÊ≠£Âú®‰∏ä‰º†ÁöÑÊñá‰ª∂Êï∞
      this.activeUploads = this.uploadTasks.filter(task => 
        task.status === 'uploading' && task.chunkQueue.some(chunk => chunk.uploading)
      ).length;
      
      this.logInfo(`ÂΩìÂâçÊ¥ªË∑É‰∏ä‰º†Êï∞: ${this.activeUploads}, ÊúÄÂ§ßÂπ∂Ë°å: ${this.maxParallelFiles}`);
      
      // Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•ÂºÄÂßãÊñ∞ÁöÑÊñá‰ª∂‰∏ä‰º†
      for (const task of this.uploadTasks) {
        if (task.status === 'uploading' && !task.chunkQueue.some(chunk => chunk.uploading)) {
          if (this.activeUploads < this.maxParallelFiles) {
            this.logInfo(`ÂºÄÂßã‰∏ä‰º†Êñá‰ª∂: ${task.name}`);
            this.uploadFileChunks(task);
            this.activeUploads++;
          }
        }
      }
      
      // Â¶ÇÊûúÊ≤°ÊúâÊ¥ªË∑É‰∏ä‰º†‰ΩÜÊúâÁ≠âÂæÖ‰∏≠ÁöÑ‰ªªÂä°ÔºåÂ∞ùËØïÂÜçÊ¨°Â§ÑÁêÜ
      if (this.activeUploads === 0) {
        const pendingTasks = this.uploadTasks.filter(task => 
          task.status === 'uploading' && task.chunkQueue.some(chunk => !chunk.uploaded)
        );
        
        if (pendingTasks.length > 0) {
          this.logInfo('ÈáçÊñ∞Â∞ùËØïÂ§ÑÁêÜÁ≠âÂæÖ‰∏≠ÁöÑ‰ªªÂä°');
          for (const task of pendingTasks.slice(0, this.maxParallelFiles)) {
            this.uploadFileChunks(task);
            this.activeUploads++;
          }
        }
      }
    },
    
    async uploadFileChunks(task) {
      this.logInfo(`Â§ÑÁêÜÊñá‰ª∂ÂàÜÁâá‰∏ä‰º†: ${task.name}`);
      
      // ËÆ°ÁÆóÂΩìÂâçÊ¥ªÂä®ÁöÑÂàÜÁâá‰∏ä‰º†Êï∞Èáè
      const activeChunks = task.chunkQueue.filter(chunk => chunk.uploading).length;
      this.logInfo(`Ê¥ªË∑ÉÂàÜÁâáÊï∞: ${activeChunks}, ÊÄªÂàÜÁâáÊï∞: ${task.totalChunks}, Â∑≤ÂÆåÊàê: ${task.uploadedChunks}`);
      
      // Â¶ÇÊûúÊâÄÊúâÂàÜÁâáÂ∑≤‰∏ä‰º†ÔºåÂÆåÊàê‰∏ä‰º†
      if (task.chunkQueue.every(chunk => chunk.uploaded)) {
        this.logInfo(`ÊâÄÊúâÂàÜÁâáÂ∑≤‰∏ä‰º†ÔºåÂÆåÊàêÊñá‰ª∂ ${task.name}`);
        await this.completeFileUpload(task);
        return;
      }
      
      // ‰∏ä‰º†Êõ¥Â§öÂàÜÁâáÔºåÁõ¥Âà∞ËææÂà∞Âπ∂Ë°åÈôêÂà∂
      let startedNewUploads = 0;
      for (let i = 0; i < task.chunkQueue.length && activeChunks + startedNewUploads < this.maxParallelChunks; i++) {
        const chunk = task.chunkQueue[i];
        if (!chunk.uploaded && !chunk.uploading) {
          this.logInfo(`ÂºÄÂßã‰∏ä‰º†ÂàÜÁâá ${i+1}/${task.totalChunks}`);
          chunk.uploading = true;
          this.uploadChunk(task, chunk);
          startedNewUploads++;
        }
      }
      
      this.logInfo(`ÂºÄÂßã‰∫Ü ${startedNewUploads} ‰∏™Êñ∞ÂàÜÁâá‰∏ä‰º†`);
    },
    
    async uploadChunk(task, chunk) {
      try {
        this.logInfo(`‰∏ä‰º†ÂàÜÁâá: Êñá‰ª∂=${task.name}, ÂàÜÁâáÁ¥¢Âºï=${chunk.index}, ÂàÜÁâáÂ§ßÂ∞è=${chunk.file.size}Â≠óËäÇ`);
        
        // ‰ΩøÁî® API ‰∏ä‰º†ÂàÜÁâá
        const response = await uploadChunk(task.taskId, chunk.index, chunk.file);
        this.logInfo(`ÂàÜÁâá‰∏ä‰º†ÂìçÂ∫î: ${JSON.stringify(response)}`);
        
        if (response.success) {
          chunk.uploaded = true;
          chunk.uploading = false;
          task.uploadedChunks++;
          
          // Êõ¥Êñ∞ËøõÂ∫¶
          task.progress = (task.uploadedChunks / task.totalChunks) * 100;
          this.logInfo(`ÂàÜÁâá‰∏ä‰º†ÊàêÂäü: ${chunk.index}, ËøõÂ∫¶: ${task.progress.toFixed(1)}%`);
          
          // ÁªßÁª≠‰∏ä‰º†Êõ¥Â§öÂàÜÁâá
          this.uploadFileChunks(task);
        } else {
          this.logWarn(`ÂàÜÁâá‰∏ä‰º†Â§±Ë¥•: ${chunk.index}, ÈîôËØØ: ${response.message}`);
          // ÈáçËØïÈÄªËæë
          if (chunk.retries < 3) {
            chunk.retries++;
            chunk.uploading = false;
            this.logInfo(`Â∞ÜÂú®1ÁßíÂêéÈáçËØïÂàÜÁâá ${chunk.index} (Â∞ùËØï ${chunk.retries}/3)`);
            // Á®çÂêéÈáçËØï
            setTimeout(() => {
              this.uploadChunk(task, chunk);
            }, 1000);
          } else {
            chunk.uploading = false;
            task.status = 'error';
            task.error = response.message || `ÂàÜÁâá ${chunk.index + 1} ‰∏ä‰º†Â§±Ë¥•`;
            this.logError(`ÂàÜÁâá ${chunk.index} ËææÂà∞ÊúÄÂ§ßÈáçËØïÊ¨°Êï∞, Ê†áËÆ∞‰ªªÂä°‰∏∫ÈîôËØØ`);
            this.checkAllTasksCompleted();
          }
        }
      } catch (error) {
        this.logError(`ÂàÜÁâá‰∏ä‰º†ÈîôËØØ: ${error.message || error}`);
        // ÈáçËØïÈÄªËæëÂêå‰∏ä
        if (chunk.retries < 3) {
          chunk.retries++;
          chunk.uploading = false;
          this.logInfo(`Â∞ÜÂú®1ÁßíÂêéÈáçËØïÂàÜÁâá ${chunk.index} (Â∞ùËØï ${chunk.retries}/3)`);
          setTimeout(() => {
            this.uploadChunk(task, chunk);
          }, 1000);
        } else {
          chunk.uploading = false;
          task.status = 'error';
          task.error = `ÂàÜÁâá ${chunk.index + 1} ‰∏ä‰º†Â§±Ë¥•`;
          this.logError(`ÂàÜÁâá ${chunk.index} ËææÂà∞ÊúÄÂ§ßÈáçËØïÊ¨°Êï∞, Ê†áËÆ∞‰ªªÂä°‰∏∫ÈîôËØØ`);
          this.checkAllTasksCompleted();
        }
      }
      
      // Ê£ÄÊü•ÂÖ∂‰ªñ‰ªªÂä°ÁöÑ‰∏ä‰º†
      this.processUploadQueue();
    },
    
    async completeFileUpload(task) {
      this.logInfo(`ÂÆåÊàêÊñá‰ª∂‰∏ä‰º†: ${task.name}, ‰ªªÂä°ID: ${task.taskId}`);
      task.status = 'verifying';
      
      try {
        this.logInfo('ÂèëÈÄÅÂÆåÊàêËØ∑Ê±Ç');
        const response = await completeChunk(task.taskId);
        this.logInfo(`ÂÆåÊàêÂìçÂ∫î: ${JSON.stringify(response)}`);
        
        if (response.success) {
          // Ê£ÄÊü•MD5ÊòØÂê¶ÂåπÈÖç
          if (response.data.mD5Verified) {
            task.status = 'completed';
            this.logInfo(`Êñá‰ª∂ ${task.name} ‰∏ä‰º†ÂÆåÊàêÂπ∂È™åËØÅÊàêÂäü`);
            
            const fileInfo = {
              fileName: response.data.originalName,
              serverFileName: response.data.serverFileName,
              size: response.data.size,
              md5: response.data.mD5,
              captureTime: response.data.captureTime || null
            };
            
            // Â¶ÇÊûúÊçïËé∑Êó∂Èó¥Â≠òÂú®ÔºåËÆ∞ÂΩïÂú®‰ªªÂä°ÂØπË±°‰∏ä
            if (response.data.captureTime) {
              task.captureTime = response.data.captureTime;
              this.logInfo(`Êñá‰ª∂ ${task.name} Âê´ÊúâÊçïËé∑Êó∂Èó¥: ${task.captureTime}`);
            }
            
            this.logInfo(`ÂèëÂá∫‰∏ä‰º†ÂÆåÊàê‰∫ã‰ª∂: ${JSON.stringify(fileInfo)}`);
            this.$emit('upload-complete', fileInfo);
          } else {
            this.logError(`MD5È™åËØÅÂ§±Ë¥•: ${JSON.stringify(response.data)}`);
            task.status = 'error';
            task.error = `MD5Ê†°È™åÂ§±Ë¥•ÔºåÊúüÊúõ: ${response.data.expectedMD5}ÔºåÂÆûÈôÖ: ${response.data.mD5}`;
          }
        } else {
          this.logError(`ÂÆåÊàê‰∏ä‰º†Â§±Ë¥•: ${JSON.stringify(response)}`);
          task.status = 'error';
          task.error = response.message || 'ÂÆåÊàê‰∏ä‰º†Â§±Ë¥•';
        }
      } catch (error) {
        this.logError(`‰∏ä‰º†ÂÆåÊàêÂ§ÑÁêÜÈîôËØØ: ${error.message || error}`);
        task.status = 'error';
        task.error = 'ÂÆåÊàê‰∏ä‰º†Â§±Ë¥•';
      }
      
      // Ê£ÄÊü•ÊòØÂê¶ÊâÄÊúâ‰ªªÂä°ÈÉΩÂ∑≤ÂÆåÊàê
      this.checkAllTasksCompleted();
      
      // Â§ÑÁêÜ‰∏ã‰∏Ä‰∏™Êñá‰ª∂‰∏ä‰º†
      this.processUploadQueue();
    },
    
    checkAllTasksCompleted() {
      this.logInfo('Ê£ÄÊü•ÊòØÂê¶ÊâÄÊúâ‰ªªÂä°ÈÉΩÂ∑≤ÂÆåÊàê');
      
      // Ê£ÄÊü•ÊâÄÊúâ‰ªªÂä°ÊòØÂê¶ÂÆåÊàê
      const isAllCompleted = this.uploadTasks.every(
        task => task.status === 'completed' || task.status === 'error'
      );
      
      this.logInfo(`ÊâÄÊúâ‰ªªÂä°ÊòØÂê¶ÂÆåÊàê: ${isAllCompleted}`);
      if (isAllCompleted && this.uploadTasks.length > 0) {
        this.logInfo('ÊâÄÊúâ‰ªªÂä°ÈÉΩÂ∑≤ÂÆåÊàêÔºåÂèëÂá∫ÂÖ®ÈÉ®ÂÆåÊàê‰∫ã‰ª∂');
        this.isUploading = false;
        
        // Êü•ÊâæÁ¨¨‰∏Ä‰∏™ÊúâÊçïËé∑Êó∂Èó¥ÁöÑ‰ªªÂä°
        let captureTime = null;
        const taskWithCaptureTime = this.uploadTasks.find(task => 
          task.status === 'completed' && task.captureTime
        );
        
        if (taskWithCaptureTime) {
          captureTime = taskWithCaptureTime.captureTime;
          this.logInfo(`ÊâæÂà∞Â∏¶ÊúâÊçïËé∑Êó∂Èó¥ÁöÑÊñá‰ª∂: ${taskWithCaptureTime.name}, Êó∂Èó¥: ${captureTime}`);
        }
        
        // ÂèëÂá∫ÂÖ®ÈÉ®ÂÆåÊàê‰∫ã‰ª∂ÔºåÂêåÊó∂‰º†ÈÄíÈ¶ñ‰∏™ÊçïËé∑Êó∂Èó¥ÔºàÂ¶ÇÊûúÊúâÔºâ
        this.$emit('all-completed', { captureTime });
        
        // Ê±áÊÄªÁªìÊûú
        const completedTasks = this.uploadTasks.filter(task => task.status === 'completed').length;
        const errorTasks = this.uploadTasks.filter(task => task.status === 'error').length;
        this.logInfo(`‰∏ä‰º†ÂÆåÊàê: ${completedTasks}‰∏™ÊàêÂäü, ${errorTasks}‰∏™Â§±Ë¥•`);
      }
    }
  }
};
</script>

