<template>
  <div class="add-edit-event">
    <!-- Snackbar ÊèêÁ§∫ÁªÑ‰ª∂ -->
    <Snackbar 
      v-model="snackbar.show"
      :message="snackbar.message" 
      :subtext="snackbar.subtext" 
      :type="snackbar.type"
      :duration="snackbar.duration"
    />

    <!-- Â§¥ÈÉ® -->
    <header class="header">
      <button class="back-btn" @click="goBack">
        ‚Üê ËøîÂõû
      </button>
      <h1 class="page-title">{{ isEdit ? 'ÁºñËæë‰∫ã‰ª∂' : 'Êñ∞Â¢û‰∫ã‰ª∂' }}</h1>
    </header>

    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <div v-if="loading" class="loading-container">
      <div class="loading-spinner">‚è≥</div>
      <p>Ê≠£Âú®Âä†ËΩΩ‰∫ã‰ª∂Êï∞ÊçÆ...</p>
    </div>

    <!-- ÈîôËØØÁä∂ÊÄÅ -->
    <div v-else-if="error && !isEdit" class="error-container">
      <div class="error-icon">‚ö†Ô∏è</div>
      <p>Âä†ËΩΩÂ§±Ë¥•</p>
      <button class="retry-btn" @click="loadEditData">ÈáçËØï</button>
    </div>

    <!-- Ë°®ÂçïÂÆπÂô® -->
    <div v-else class="form-container">

      
      <form @submit.prevent="saveEvent" class="event-form">
        <!-- Âü∫Êú¨‰ø°ÊÅØ -->
        <div class="form-section">
          <h3 class="section-title">Âü∫Êú¨‰ø°ÊÅØ</h3>
          
          <div class="form-group">
            <label class="form-label" for="title">‰∫ã‰ª∂Ê†áÈ¢ò *</label>
            <input 
              type="text" 
              id="title"
              v-model="formData.title"
              class="form-input"
              placeholder="ËØ∑ËæìÂÖ•‰∫ã‰ª∂Ê†áÈ¢ò"
              required
            />
          </div>

          <div class="form-group">
            <label class="form-label" for="date">‰∫ã‰ª∂Êó•Êúü *</label>
            <input 
              type="date" 
              id="date"
              v-model="formData.date"
              class="form-input"
              required
            />
          </div>

          <div class="form-group">
            <label class="form-label" for="location">Âú∞ÁÇπ</label>
            <input 
              type="text" 
              id="location"
              v-model="formData.location"
              class="form-input"
              placeholder="ËØ∑ËæìÂÖ•Âú∞ÁÇπ"
            />
          </div>

          <div class="form-group">
            <label class="form-label" for="description">ËØ¶ÁªÜÊèèËø∞</label>
            <textarea 
              id="description"
              v-model="formData.description"
              class="form-textarea"
              rows="4"
              placeholder="ËØ∑ËæìÂÖ•ËØ¶ÁªÜÊèèËø∞"
            ></textarea>
          </div>
        </div>

        <!-- ÁÖßÁâá‰∏ä‰º† -->
        <div class="form-section">
          <h3 class="section-title">ÁÖßÁâá</h3>
          
          <div class="upload-area" @click="!uploading && triggerImageUpload()" :class="{ uploading }">
            <input 
              type="file" 
              ref="imageInput" 
              @change="handleImageUpload"
              accept="image/*"
              multiple
              style="display: none;"
              :disabled="uploading"
            />
            <div class="upload-placeholder">
              <span v-if="uploading">‚è≥</span>
              <span v-else>üì∑</span>
              <p>{{ uploading ? 'Ê≠£Âú®‰∏ä‰º†ÁÖßÁâá...' : 'ÁÇπÂáª‰∏ä‰º†ÁÖßÁâá' }}</p>
              <span class="upload-hint" v-if="!uploading">ÊîØÊåÅÂ§öÂº†ÂõæÁâá‰∏ä‰º†</span>
            </div>
          </div>

          <div class="media-list" v-if="formData.media.images.length > 0">
            <div 
              v-for="(image, index) in formData.media.images" 
              :key="`image-${index}`"
              class="media-item"
            >
              <div 
                class="media-preview"
                :style="image.fileName && isEdit ? { backgroundImage: `url(${getMediaUrl(route.params.id, image.fileName)})` } : {}"
              >
                <span v-if="!image.fileName || !isEdit" class="media-icon">üì∑</span>
              </div>
              <div class="media-info">
                <input 
                  type="text" 
                  v-model="image.desc"  
                  class="media-desc-input"
                  placeholder="ËØ∑ËæìÂÖ•ÂõæÁâáÊèèËø∞"
                />
              </div>
              <button 
                type="button" 
                class="remove-btn"
                @click="removeMedia('images', index)"
              >
                ‚úï
              </button>
            </div>
          </div>
        </div>

        <!-- ËßÜÈ¢ë‰∏ä‰º† -->
        <div class="form-section">
          <h3 class="section-title">ËßÜÈ¢ë</h3>
          
          <div class="upload-area" @click="!uploading && triggerVideoUpload()" :class="{ uploading }">
            <input 
              type="file" 
              ref="videoInput" 
              @change="handleVideoUpload"
              accept="video/*"
              multiple
              style="display: none;"
              :disabled="uploading"
            />
            <div class="upload-placeholder">
              <span v-if="uploading">‚è≥</span>
              <span v-else>üé¨</span>
              <p>{{ uploading ? 'Ê≠£Âú®‰∏ä‰º†ËßÜÈ¢ë...' : 'ÁÇπÂáª‰∏ä‰º†ËßÜÈ¢ë' }}</p>
              <span class="upload-hint" v-if="!uploading">ÊîØÊåÅÂ§ö‰∏™ËßÜÈ¢ë‰∏ä‰º†</span>
            </div>
          </div>

          <div class="media-list" v-if="formData.media.videos.length > 0">
            <div 
              v-for="(video, index) in formData.media.videos" 
              :key="`video-${index}`"
              class="media-item"
            >
              <div class="media-preview video">
                <span class="media-icon">üé¨</span>
              </div>
              <div class="media-info">
                <input 
                  type="text" 
                  v-model="video.desc"  
                  class="media-desc-input"
                  placeholder="ËØ∑ËæìÂÖ•ËßÜÈ¢ëÊèèËø∞"
                />
              </div>
              <button 
                type="button" 
                class="remove-btn"
                @click="removeMedia('videos', index)"
              >
                ‚úï
              </button>
            </div>
          </div>
        </div>

        <!-- ÂΩïÈü≥ÂäüËÉΩ -->
        <div class="form-section">
          <h3 class="section-title">ÂΩïÈü≥</h3>
          
          <!-- ÂΩïÈü≥ÊéßÂà∂Âå∫Âüü -->
          <div class="recording-controls">
            <button 
              type="button" 
              class="record-btn"
              :class="{ recording: isRecording, disabled: uploading }"
              @click="toggleRecording"
              :disabled="uploading"
            >
              <span v-if="isRecording">‚èπÔ∏è ÂÅúÊ≠¢ÂΩïÈü≥</span>
              <span v-else-if="uploading">‚è≥ ‰∏ä‰º†‰∏≠...</span>
              <span v-else>üé§ ÂºÄÂßãÂΩïÈü≥</span>
            </button>
            
            <div v-if="isRecording" class="recording-info">
              <span class="recording-time">{{ formatTime(recordingTime) }}</span>
              <span class="recording-indicator">üî¥ ÂΩïÈü≥‰∏≠</span>
            </div>
          </div>

          <!-- Êñá‰ª∂‰∏ä‰º†Â§áÈÄâ -->
          <div class="upload-area secondary" @click="!uploading && !isRecording && triggerAudioUpload()" :class="{ uploading, disabled: isRecording }">
            <input 
              type="file" 
              ref="audioInput" 
              @change="handleAudioUpload"
              accept="audio/*"
              multiple
              style="display: none;"
              :disabled="uploading || isRecording"
            />
            <div class="upload-placeholder">
              <span>üìÅ</span>
              <p>ÊàñËÄÖÁÇπÂáª‰∏ä‰º†Èü≥È¢ëÊñá‰ª∂</p>
              <span class="upload-hint">ÊîØÊåÅÂ§öÊÆµÈü≥È¢ë‰∏ä‰º†</span>
            </div>
          </div>

          <div class="media-list" v-if="formData.media.audios.length > 0">
            <div 
              v-for="(audio, index) in formData.media.audios" 
              :key="`audio-${index}`"
              class="media-item"
            >
              <div class="media-preview audio">
                <span class="media-icon">üéô</span>
              </div>
              <div class="media-info">
                <input 
                  type="text" 
                  v-model="audio.desc"  
                  class="media-desc-input"
                  placeholder="ËØ∑ËæìÂÖ•Èü≥È¢ëÊèèËø∞"
                />
              </div>
              <button 
                type="button" 
                class="remove-btn"
                @click="removeMedia('audios', index)"
              >
                ‚úï
              </button>
            </div>
          </div>
        </div>

        <!-- Êèê‰∫§ÊåâÈíÆ -->
        <div class="form-actions">
          <button type="button" class="cancel-btn" @click="goBack">
            ÂèñÊ∂à
          </button>
          <button type="submit" class="save-btn" :disabled="!isFormValid || saving">
            {{ saving ? '‰øùÂ≠ò‰∏≠...' : (isEdit ? '‰øùÂ≠ò‰øÆÊîπ' : 'ÂàõÂª∫‰∫ã‰ª∂') }}
          </button>
        </div>
      </form>
      
      <!-- ÂàÜÁâá‰∏ä‰º†ÁªÑ‰ª∂ -->
      <div v-if="showChunkUploader" class="chunk-uploader-overlay">
        <div class="chunk-uploader-container">
          <h3>Êñá‰ª∂ÂàÜÁâá‰∏ä‰º†</h3>
          <ChunkUploader
            :maxParallelFiles="3"
            :maxParallelChunks="3"
            :chunkSize="2 * 1024 * 1024"
            @upload-complete="handleUploadComplete"
            @all-completed="handleAllUploadsCompleted"
            ref="chunkUploader"
          />
          <!-- ‰∏ä‰º†Áä∂ÊÄÅÊåáÁ§∫Âô® -->
          <div class="chunk-uploader-status">
            <div v-if="uploading" class="upload-status-indicator">
              ‰∏ä‰º†‰∏≠...ËØ∑Á≠âÂæÖ
            </div>
            <div v-else class="upload-status-completed">
              ‰∏ä‰º†Â∑≤ÂÆåÊàê
            </div>
          </div>
          <div v-if="uploading && uploaderStatus.taskCount === 0" class="upload-error">
            <p>‰∏ä‰º†ÁªÑ‰ª∂Êú™Ê≠£Á°ÆÂàùÂßãÂåñÊàñÊú™Ê∑ªÂä†Êñá‰ª∂</p>
            <button type="button" class="retry-btn" @click="retryUpload">ÈáçËØï‰∏ä‰º†</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted, nextTick } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import dayjs from 'dayjs'
import { getEventById, createEvent, updateEvent, getMediaUrl } from '@/api/events'
import AudioRecorder from '@/utils/AudioRecorder'
import ChunkUploader from '@/components/ChunkUploader.vue'
import Snackbar from '@/components/Snackbar.vue'

export default {
  name: 'AddEditEvent',
  components: {
    ChunkUploader,
    Snackbar
  },
  setup() {
    const route = useRoute()
    const router = useRouter()
    
    const imageInput = ref(null)
    const videoInput = ref(null)
    const audioInput = ref(null)
    const loading = ref(false)
    const saving = ref(false)
    const uploading = ref(false)
    const error = ref('')
    
    // ÂàÜÁâá‰∏ä‰º†Áõ∏ÂÖ≥Áä∂ÊÄÅ
    const showChunkUploader = ref(false)
    const uploadFiles = ref([])
    const currentMediaType = ref('')
    const chunkUploader = ref(null) // ÂàÜÁâá‰∏ä‰º†ÁªÑ‰ª∂ÁöÑÂºïÁî®
    
    // ÂΩïÈü≥Áõ∏ÂÖ≥Áä∂ÊÄÅ
    const isRecording = ref(false)
    const recordingTime = ref(0)
    const audioRecorder = ref(null)
    
    // SnackbarÊèêÁ§∫Áõ∏ÂÖ≥Áä∂ÊÄÅ
    const snackbar = ref({
      show: false,
      message: '',
      subtext: '',
      type: 'info',
      duration: 2000
    })
    
    const isEdit = computed(() => !!route.params.id)
    
    // Ë°®ÂçïÊï∞ÊçÆ
    const formData = ref({
      title: '',
      date: dayjs().format('YYYY-MM-DD'),
      location: '',
      description: '',
      media: {
        images: [],
        videos: [],
        audios: []
      }
    })

    // Ë°®ÂçïÈ™åËØÅ
    const isFormValid = computed(() => {
      return formData.value.title.trim() !== '' && formData.value.date !== ''
    })
    
    // ‰∏ä‰º†ÁªÑ‰ª∂Áä∂ÊÄÅ
    const uploaderStatus = computed(() => {
      if (!chunkUploader.value) {
        return { exists: false, taskCount: 0, isUploading: false };
      }
      
      return {
        exists: true,
        taskCount: chunkUploader.value.uploadTasks?.length || 0,
        isUploading: chunkUploader.value.isUploading || false
      };
    })

    // Âä†ËΩΩÁºñËæëÊï∞ÊçÆ
    const loadEditData = async () => {
      if (isEdit.value) {
      try {
        loading.value = true
          
          const eventId = route.params.id
          const response = await getEventById(eventId)
          
          if (response.success) {
            const event = response.data
            formData.value = {
              title: event.title,
              date: event.date,
              location: event.location || '',
              description: event.description || '',
              media: {
                images: [...event.media.images],
                videos: [...event.media.videos],
                audios: [...event.media.audios]
              }
            }
          } else {
            alert(response.message || 'Ëé∑Âèñ‰∫ã‰ª∂Êï∞ÊçÆÂ§±Ë¥•')
            // Â¶ÇÊûú‰∫ã‰ª∂‰∏çÂ≠òÂú®ÔºåËøîÂõûÈ¶ñÈ°µ
            if (response.message === '‰∫ã‰ª∂‰∏çÂ≠òÂú®') {
              setTimeout(() => {
                router.push('/')
              }, 1000)
            }
          }
        } catch (err) {
          alert('ÁΩëÁªúÈîôËØØÔºåËØ∑Á®çÂêéÈáçËØï')
          console.error('Âä†ËΩΩÁºñËæëÊï∞ÊçÆÂ§±Ë¥•:', err)
        } finally {
          loading.value = false
        }
      }
    }

    // Ëß¶ÂèëÊñá‰ª∂‰∏ä‰º†
    const triggerImageUpload = () => {
      imageInput.value.click()
    }

    const triggerVideoUpload = () => {
      videoInput.value.click()
    }

    const triggerAudioUpload = () => {
      audioInput.value.click()
    }

    // Â§ÑÁêÜÊñá‰ª∂‰∏ä‰º† - ‰ΩøÁî®ÂàÜÁâá‰∏ä‰º†
    // ÂÖ¨ÂÖ±‰∏ä‰º†Â§ÑÁêÜÂáΩÊï∞
    const handleChunkUpload = (files, mediaType) => {
      if (!files || files.length === 0) {
        console.log('Ê≤°ÊúâÈÄâÊã©Êñá‰ª∂');
        return;
      }
      
      console.log(`Â§ÑÁêÜÊñá‰ª∂‰∏ä‰º†: Á±ªÂûã=${mediaType}, Êñá‰ª∂Êï∞=${files.length}`);
      files.forEach((file, index) => {
        console.log(`Êñá‰ª∂[${index}]: ÂêçÁß∞=${file.name}, Â§ßÂ∞è=${file.size}Â≠óËäÇ, Á±ªÂûã=${file.type}`);
      });
      
      // ‰øùÂ≠òÊñá‰ª∂ÂíåÂ™í‰ΩìÁ±ªÂûãÔºåÁî®‰∫éÁ®çÂêé‰∏ä‰º†
      uploadFiles.value = [...files]; 
      currentMediaType.value = mediaType;
      
      // ÊòæÁ§∫‰∏ä‰º†ÂØπËØùÊ°Ü
      showChunkUploader.value = true;
      uploading.value = true;
      
      // ‰ΩøÁî®‰∏§Â±ÇnextTickÁ°Æ‰øùÁªÑ‰ª∂ÂÆåÂÖ®Ê∏≤ÊüìÂêéÂÜçÊ∑ªÂä†Êñá‰ª∂
      // Á¨¨‰∏Ä‰∏™nextTickÁ≠âÂæÖshowChunkUploaderÁöÑÂèòÂåñÁîüÊïà
      nextTick(() => {
        console.log('Á¨¨‰∏Ä‰∏™nextTick: Ê£ÄÊü•‰∏ä‰º†ÁªÑ‰ª∂ÊòØÂê¶Â≠òÂú®:', !!chunkUploader.value);
        
        // Á¨¨‰∫å‰∏™nextTickÁ°Æ‰øùÁªÑ‰ª∂Â∑≤ÂÆåÂÖ®ÂàùÂßãÂåñ
        nextTick(() => {
          console.log('Á¨¨‰∫å‰∏™nextTick: ÂÜçÊ¨°Ê£ÄÊü•ÁªÑ‰ª∂:', !!chunkUploader.value);
          
          if (chunkUploader.value) {
            console.log('Ê∑ªÂä†Êñá‰ª∂Âà∞‰∏ä‰º†ÁªÑ‰ª∂');
            // Ê∑ªÂä†‰øùÂ≠òÁöÑÊñá‰ª∂Âà∞‰∏ä‰º†ÁªÑ‰ª∂
            uploadFiles.value.forEach((file, index) => {
              console.log(`Ê∑ªÂä†Êñá‰ª∂[${index}]: ${file.name} Âà∞‰∏ä‰º†ÁªÑ‰ª∂`);
              try {
                chunkUploader.value.addFile(file);
                console.log(`Êñá‰ª∂[${index}]Ê∑ªÂä†ÊàêÂäü`);
              } catch (error) {
                console.error(`Ê∑ªÂä†Êñá‰ª∂[${index}]Â§±Ë¥•:`, error);
              }
            });
          } else {
            console.error('‰∏§Ê¨°nextTickÂêéÁªÑ‰ª∂ÂºïÁî®‰ªç‰∏çÂ≠òÂú®!');
            // Â∞ùËØïÁõ¥Êé•Ëé∑ÂèñÁªÑ‰ª∂
            const uploader = document.querySelector('.chunk-uploader');
            console.log('DOM‰∏≠ÊòØÂê¶Â≠òÂú®‰∏ä‰º†ÁªÑ‰ª∂:', !!uploader);
          }
        });
      });
    }
    
    // Â§ÑÁêÜÂõæÁâá‰∏ä‰º†
    const handleImageUpload = (event) => {
      const files = Array.from(event.target.files);
      handleChunkUpload(files, 'images');
      event.target.value = ''; // Ê∏ÖÁ©∫input
    }

    // Â§ÑÁêÜËßÜÈ¢ë‰∏ä‰º†
    const handleVideoUpload = (event) => {
      const files = Array.from(event.target.files);
      handleChunkUpload(files, 'videos');
      event.target.value = '';
    }

    // Â§ÑÁêÜÈü≥È¢ë‰∏ä‰º†
    const handleAudioUpload = (event) => {
      const files = Array.from(event.target.files);
      handleChunkUpload(files, 'audios');
      event.target.value = '';
    }
    
    // Â§ÑÁêÜÂàÜÁâá‰∏ä‰º†ÂÆåÊàê
    const handleUploadComplete = (fileInfo) => {
      console.log('Êî∂Âà∞Âçï‰∏™Êñá‰ª∂‰∏ä‰º†ÂÆåÊàê‰∫ã‰ª∂:', fileInfo);
      
      // Ê†πÊçÆÂΩìÂâçÂ™í‰ΩìÁ±ªÂûãÔºåÊ∑ªÂä†Âà∞ÂØπÂ∫îÁöÑÂ™í‰ΩìÂàóË°®‰∏≠
      if (currentMediaType.value) {
        console.log(`Â∞ÜÊñá‰ª∂Ê∑ªÂä†Âà∞Â™í‰ΩìÁ±ªÂûã: ${currentMediaType.value}`);
        
        // Ê£ÄÊü•ÊòØÂê¶ÊòØÂΩïÈü≥Êñá‰ª∂ÔºåÂ¶ÇÊûúÊòØÂàôÊ∑ªÂä†ÁâπÊÆäÊèèËø∞
        let desc = fileInfo.fileName || '';
        if (currentMediaType.value === 'audios' && uploadFiles.value.recordingDuration) {
          desc = `ÂΩïÈü≥ (${formatTime(uploadFiles.value.recordingDuration)})`;
          console.log(`ËÆæÁΩÆÂΩïÈü≥ÊèèËø∞: ${desc}`);
        }
        
        // Â¶ÇÊûúÊñá‰ª∂ÊúâÊçïËé∑Êó∂Èó¥ÔºåËÆ∞ÂΩïÂú®ÊéßÂà∂Âè∞
        if (fileInfo.captureTime) {
          console.log(`Êñá‰ª∂ ${fileInfo.fileName} ÊúâÊçïËé∑Êó∂Èó¥: ${fileInfo.captureTime}`);
        }
        
        const mediaItem = {
          fileName: fileInfo.serverFileName, // ‰ΩøÁî®ÊúçÂä°Âô®Á´ØÊñá‰ª∂Âêç
          desc: desc,
          size: fileInfo.size,
          uploadTime: new Date().toISOString(),
          captureTime: fileInfo.captureTime || null // ‰øùÂ≠òÊçïËé∑Êó∂Èó¥‰ø°ÊÅØ
        };
        
        console.log('Ê∑ªÂä†Â™í‰ΩìÈ°π:', mediaItem);
        formData.value.media[currentMediaType.value].push(mediaItem);
        console.log(`ÂΩìÂâç${currentMediaType.value}Êï∞Èáè: ${formData.value.media[currentMediaType.value].length}`);
        
        // ÊòæÁ§∫‰∏ä‰º†ÊàêÂäüÊèêÁ§∫
        let mediaTypeText = '';
        switch (currentMediaType.value) {
          case 'images': mediaTypeText = 'ÂõæÁâá'; break;
          case 'videos': mediaTypeText = 'ËßÜÈ¢ë'; break;
          case 'audios': mediaTypeText = 'Èü≥È¢ë'; break;
        }
        
        showSnackbar(`${mediaTypeText}‰∏ä‰º†ÊàêÂäü`, {
          subtext: fileInfo.fileName,
          type: 'info',
          duration: 1000
        });
      } else {
        console.warn('Êñá‰ª∂‰∏ä‰º†ÂÆåÊàê‰ΩÜÂ™í‰ΩìÁ±ªÂûãÊú™ËÆæÁΩÆ!');
        showSnackbar('Êñá‰ª∂‰∏ä‰º†ÂÆåÊàê', {
          type: 'warning',
          subtext: 'Â™í‰ΩìÁ±ªÂûãÊú™ËÆæÁΩÆÔºåËØ∑ÈáçËØï',
          duration: 3000
        });
      }
    }
    
    // ÊâÄÊúâÊñá‰ª∂‰∏ä‰º†ÂÆåÊàê
    const handleAllUploadsCompleted = (data) => {
      console.log('ÊâÄÊúâÊñá‰ª∂‰∏ä‰º†ÂÆåÊàê', data);
      uploading.value = false;
      
      // Ê£ÄÊü•Âπ∂Â§ÑÁêÜÊçïËé∑Êó∂Èó¥
      if (data && data.captureTime) {
        console.log(`Ëé∑ÂèñÂà∞ÊçïËé∑Êó∂Èó¥: ${data.captureTime}`);
        // Â∞ÜÊçïËé∑Êó∂Èó¥ËΩ¨Êç¢‰∏∫Êó•ÊúüÊ†ºÂºè YYYY-MM-DD Âπ∂Êõ¥Êñ∞Ë°®ÂçïÊó•Êúü
        try {
          const captureDate = dayjs(data.captureTime).format('YYYY-MM-DD');
          const oldDate = formData.value.date;
          console.log(`Â∞Ü‰∫ã‰ª∂Êó•Êúü‰ªé ${oldDate} Êõ¥Êñ∞‰∏∫ ${captureDate}`);
          formData.value.date = captureDate;
          
          // ‰ΩøÁî®ËæÖÂä©ÂáΩÊï∞ÊòæÁ§∫SnackbarÊèêÁ§∫
          showSnackbar('Â∑≤‰ªéÂ™í‰ΩìÊñá‰ª∂‰∏≠Ê£ÄÊµãÂà∞Êó•Êúü', {
            subtext: `‰∫ã‰ª∂Êó•ÊúüÂ∑≤Ëá™Âä®Êõ¥Êñ∞‰∏∫: ${captureDate}`,
            type: 'success',
            duration: 1000
          });
        } catch (error) {
          console.error('Êó†Ê≥ïËß£ÊûêÊçïËé∑Êó∂Èó¥:', error);
          
          // ‰ΩøÁî®ËæÖÂä©ÂáΩÊï∞ÊòæÁ§∫ÈîôËØØÊèêÁ§∫
          showSnackbar('Êó•ÊúüÊ†ºÂºèËß£ÊûêÂ§±Ë¥•', {
            subtext: 'Êó†Ê≥ï‰ªéÂ™í‰ΩìÊñá‰ª∂‰∏≠ÊèêÂèñÊó•Êúü‰ø°ÊÅØ',
            type: 'error',
            duration: 4000
          });
        }
      }
      
      // Ê∏ÖÈô§ÂΩïÈü≥ÊåÅÁª≠Êó∂Èó¥
      if (uploadFiles.value.recordingDuration) {
        uploadFiles.value.recordingDuration = null;
        console.log('Ê∏ÖÈô§ÂΩïÈü≥ÊåÅÁª≠Êó∂Èó¥');
      }
      
      recordingTime.value = 0;
      
      // Âª∂ËøüÈöêËóè‰∏ä‰º†Âå∫ÂüüÔºåÁªôÁî®Êà∑‰∏ÄÁÇπÊó∂Èó¥ÁúãÂà∞ÂÆåÊàêÁä∂ÊÄÅ
      setTimeout(() => {
        console.log('ÈöêËóè‰∏ä‰º†Âå∫Âüü');
        showChunkUploader.value = false;
      }, 1000);
    }
    
    // ÈáçËØï‰∏ä‰º†
    const retryUpload = () => {
      console.log('ÈáçËØï‰∏ä‰º†, Êñá‰ª∂Êï∞:', uploadFiles.value.length);
      
      if (!uploadFiles.value || uploadFiles.value.length === 0) {
        console.warn('Ê≤°ÊúâÂæÖ‰∏ä‰º†Êñá‰ª∂ÔºåÊó†Ê≥ïÈáçËØï');
        return;
      }
      
      // Ê∑ªÂä†Â∞èÂª∂Ëøü‰ª•Á°Æ‰øùÁªÑ‰ª∂Â∑≤ÂÆåÂÖ®ÂàùÂßãÂåñ
      nextTick(() => {
        if (chunkUploader.value) {
          console.log('Â∞ùËØïÈáçÊñ∞Ê∑ªÂä†Êñá‰ª∂Âà∞‰∏ä‰º†ÁªÑ‰ª∂');
          uploadFiles.value.forEach((file, index) => {
            console.log(`ÈáçÊñ∞Ê∑ªÂä†Êñá‰ª∂[${index}]: ${file.name}`);
            try {
              chunkUploader.value.addFile(file);
            } catch (error) {
              console.error(`ÈáçÊñ∞Ê∑ªÂä†Êñá‰ª∂Â§±Ë¥•:`, error);
            }
          });
        } else {
          console.error('‰∏ä‰º†ÁªÑ‰ª∂‰ªç‰∏çÂèØÁî®');
        }
      });
    }

    // ÂΩïÈü≥Áõ∏ÂÖ≥ÊñπÊ≥ï
    const initAudioRecorder = () => {
      if (!audioRecorder.value) {
        audioRecorder.value = new AudioRecorder()
        
        // ËÆæÁΩÆÂΩïÈü≥ÂÆåÊàêÂõûË∞É
        audioRecorder.value.onStop = async (audioBlob, duration) => {
          await uploadRecordedAudio(audioBlob, duration)
        }
        
        // ËÆæÁΩÆÂΩïÈü≥Êó∂Èó¥Êõ¥Êñ∞ÂõûË∞É
        audioRecorder.value.onTimeUpdate = (time) => {
          recordingTime.value = time
        }
        
        // ËÆæÁΩÆÈîôËØØÂõûË∞É
        audioRecorder.value.onError = (err) => {
          alert(`ÂΩïÈü≥Â§±Ë¥•: ${err.message || 'Êú™Áü•ÈîôËØØ'}`)
          isRecording.value = false
          recordingTime.value = 0
        }
      }
    }

    const startRecording = async () => {
      try {
        if (!AudioRecorder.isSupported()) {
          alert('ÂΩìÂâçÊµèËßàÂô®‰∏çÊîØÊåÅÂΩïÈü≥ÂäüËÉΩÔºåËØ∑‰ΩøÁî®Áé∞‰ª£ÊµèËßàÂô®')
          return
        }

        initAudioRecorder()
        await audioRecorder.value.startRecording()
        isRecording.value = true
        recordingTime.value = 0
      } catch (err) {
        alert(err.message || 'Êó†Ê≥ïÂêØÂä®ÂΩïÈü≥ÔºåËØ∑Ê£ÄÊü•È∫¶ÂÖãÈ£éÊùÉÈôê')
        isRecording.value = false
        console.error('ÂΩïÈü≥ÂêØÂä®Â§±Ë¥•:', err)
      }
    }

    const stopRecording = () => {
      if (audioRecorder.value && isRecording.value) {
        audioRecorder.value.stopRecording()
        isRecording.value = false
      }
    }

    const toggleRecording = () => {
      if (isRecording.value) {
        stopRecording()
      } else {
        startRecording()
      }
    }

    const uploadRecordedAudio = async (audioBlob, duration) => {
      try {
        // ÂàõÂª∫Êñá‰ª∂Âêç
        const timestamp = new Date().toISOString().replace(/[:.]/g, '-')
        const extension = audioRecorder.value.getFileExtension()
        const fileName = `recording_${timestamp}.${extension}`
        
        // ÂàõÂª∫FileÂØπË±°
        const audioFile = new File([audioBlob], fileName, { 
          type: audioBlob.type 
        })

        // ËÆæÁΩÆÂΩìÂâçÂ™í‰ΩìÁ±ªÂûã‰∏∫Èü≥È¢ë
        currentMediaType.value = 'audios'
        
        // ËÆ∞ÂΩïÂΩïÈü≥ÊåÅÁª≠Êó∂Èó¥ÔºåÁî®‰∫éÂêéÁª≠ÊèèËø∞
        uploadFiles.value = [audioFile]
        uploadFiles.value.recordingDuration = duration
        
        // ÊòæÁ§∫‰∏ä‰º†ÂØπËØùÊ°ÜÂπ∂ËÆæÁΩÆ‰∏ä‰º†‰∏≠Áä∂ÊÄÅ
        showChunkUploader.value = true
        uploading.value = true
        
        // Ê∑ªÂä†Êñá‰ª∂Âà∞‰∏ä‰º†ÁªÑ‰ª∂ (‰ºöËá™Âä®ÂºÄÂßã‰∏ä‰º†)
        nextTick(() => {
          if (chunkUploader.value) {
            chunkUploader.value.addFile(audioFile);
          }
        });
      } catch (err) {
        alert('ÁΩëÁªúÈîôËØØÔºåÂΩïÈü≥‰∏ä‰º†Â§±Ë¥•')
        console.error('ÂΩïÈü≥‰∏ä‰º†Â§±Ë¥•:', err)
        recordingTime.value = 0
      }
    }

    // Ê†ºÂºèÂåñÊó∂Èó¥ÊòæÁ§∫
    const formatTime = (seconds) => {
      const mins = Math.floor(seconds / 60)
      const secs = seconds % 60
      return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
    }
    
    // ÊòæÁ§∫SnackbarÊ∂àÊÅØÁöÑËæÖÂä©ÂáΩÊï∞
    const showSnackbar = (message, options = {}) => {
      // ÂÖàÈáçÁΩÆshow‰∏∫falseÔºåÂº∫Âà∂Ëß¶ÂèëÂèòÊõ¥
      snackbar.value.show = false;
      
      // ‰ΩøÁî®nextTickÁ°Æ‰øùDOMÊõ¥Êñ∞ÂêéÂÜçÊòæÁ§∫
      nextTick(() => {
        snackbar.value = {
          show: true,
          message,
          subtext: options.subtext || '',
          type: options.type || 'info',
          duration: options.duration || 4000
        };
        
        console.log('ÊòæÁ§∫Snackbar:', snackbar.value);
      });
    }

    // ÁßªÈô§Â™í‰ΩìÊñá‰ª∂
    const removeMedia = (type, index) => {
      formData.value.media[type].splice(index, 1)
    }

    // ‰øùÂ≠ò‰∫ã‰ª∂
    const saveEvent = async () => {
      if (!isFormValid.value || saving.value) return

      try {
        saving.value = true

        const eventData = {
          title: formData.value.title,
          date: formData.value.date,
          location: formData.value.location,
          description: formData.value.description,
          media: {
            images: formData.value.media.images.map(img => ({
              fileName: img.fileName, // ËøôÈáå‰øùÂ≠òÁöÑÊòØÊúçÂä°Âô®Á´ØÊñá‰ª∂Âêç
              desc: img.desc,
              hash:'',
              captureTime:null
            })),
            videos: formData.value.media.videos.map(vid => ({
              fileName: vid.fileName, // ËøôÈáå‰øùÂ≠òÁöÑÊòØÊúçÂä°Âô®Á´ØÊñá‰ª∂Âêç
              desc: vid.desc,
              hash:'',
              captureTime:null
            })),
            audios: formData.value.media.audios.map(aud => ({
              fileName: aud.fileName, // ËøôÈáå‰øùÂ≠òÁöÑÊòØÊúçÂä°Âô®Á´ØÊñá‰ª∂Âêç
              desc: aud.desc,
              hash:'',
              captureTime:null
            }))
          }
        }

        let response
        if (isEdit.value) {
          response = await updateEvent(route.params.id, eventData)
        } else {
          response = await createEvent(eventData)
        }

        if (response.success) {
          // ‰øùÂ≠òÊàêÂäüÔºåËøîÂõûÈ¶ñÈ°µÂπ∂ÂÆö‰ΩçÂà∞ËØ•‰∫ã‰ª∂
          const eventId = isEdit.value ? route.params.id : response.data.id
          router.push({
            path: '/',
            query: { highlight: eventId }
          })
        } else {
          alert(response.message || '‰øùÂ≠òÂ§±Ë¥•')
        }
      } catch (err) {
        alert('ÁΩëÁªúÈîôËØØÔºå‰øùÂ≠òÂ§±Ë¥•')
        console.error('‰øùÂ≠ò‰∫ã‰ª∂Â§±Ë¥•:', err)
      } finally {
        saving.value = false
      }
    }

    // ËøîÂõû
    const goBack = () => {
      if (isEdit.value) {
        // ÁºñËæëÊ®°Âºè‰∏ãËøîÂõûÂà∞ËØ¶ÊÉÖÈ°µÈù¢
        router.push(`/event/${route.params.id}`)
      } else {
        // Êñ∞Â¢ûÊ®°Âºè‰∏ãËøîÂõûÂà∞È¶ñÈ°µ
        router.push('/')
      }
    }

    onMounted(async () => {
      await loadEditData()
    })

    onUnmounted(() => {
      // Ê∏ÖÁêÜÂΩïÈü≥ËµÑÊ∫ê
      if (audioRecorder.value) {
        audioRecorder.value.destroy()
      }
    })

    return {
      isEdit,
      formData,
      isFormValid,
      loading,
      saving,
      uploading,
      error,
      imageInput,
      videoInput,
      audioInput,
      isRecording,
      recordingTime,
      showChunkUploader,
      uploadFiles,
      currentMediaType,
      uploaderStatus,
      snackbar,
      triggerImageUpload,
      triggerVideoUpload,
      triggerAudioUpload,
      handleImageUpload,
      handleVideoUpload,
      handleAudioUpload,
      handleUploadComplete,
      handleAllUploadsCompleted,
      toggleRecording,
      formatTime,
      removeMedia,
      saveEvent,
      goBack,
      loadEditData,
      getMediaUrl,
      route,
      retryUpload,
      chunkUploader,
      showSnackbar
    }
  }
}
</script>

