<template>
  <div class="video-modal" v-if="show">
    <!-- Snackbar ÊèêÁ§∫ÁªÑ‰ª∂ -->
    <Snackbar 
      v-model="snackbar.show"
      :message="snackbar.message" 
      :subtext="snackbar.subtext" 
      :type="snackbar.type"
      :duration="snackbar.duration"
    />
    <div class="modal-backdrop" @click="close"></div>
    <div class="modal-content">
      <button class="modal-close" @click="close">‚úï</button>
      <button class="nav-btn prev" @click.stop="prevVideo" v-if="currentIndex > 0">‚Äπ</button>
      <button class="nav-btn next" @click.stop="nextVideo" v-if="currentIndex < totalVideos - 1">‚Ä∫</button>
      <p class="video-counter">{{ currentIndex + 1 }} / {{ totalVideos }}</p>
      <div class="swipe-hint">
        <span class="hint-text">‚Üê Â∑¶Âè≥ÂàáÊç¢ËßÜÈ¢ë ‚Üí</span>
      </div>
      
      <div 
        class="large-video-container"
        @touchstart="handleTouchStart" 
        @touchmove="handleTouchMove" 
        @touchend="handleTouchEnd"
      >
        <!-- Video player -->
        <div 
          ref="videoContainer" 
          class="video-player-container"
          v-show="currentVideo"
        >
          <div ref="playerWrapper">
            <video
              id="video-player"
              ref="videoPlayer"
              class="video-js vjs-big-play-centered"
            ></video>
          </div>
        </div>
        
        <!-- Video info -->
        <div class="video-info">
          <h2 class="video-title">{{ currentVideo?.desc || '' }}</h2>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, watch, computed, onMounted, onBeforeUnmount, nextTick } from 'vue'
import Snackbar from '@/components/Snackbar.vue'
import videojs from 'video.js'
import 'video.js/dist/video-js.css'
import { initializeVideoPlayer, formatDuration } from '@/utils/videoPlayerUtils'
import VideoService from '@/services/videoService'
import logger from '@/utils/videoLogger'
import { createPhotoViewerGesture } from '@/utils/touchGestureManager'

export default {
  name: 'VideoViewer',
  components: {
    Snackbar
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    videos: {
      type: Array,
      required: true
    },
    initialIndex: {
      type: Number,
      default: 0
    },
    eventId: {
      type: String,
      required: true
    },
    getVideoUrl: {
      type: Function,
      required: true
    }
  },
  emits: ['close', 'indexChange'],
  setup(props, { emit }) {
    const currentIndex = ref(props.initialIndex)
    // SnackbarÁõ∏ÂÖ≥Áä∂ÊÄÅ
    const snackbar = ref({
      show: false,
      message: '',
      subtext: '',
      type: 'info',
      duration: 4000
    })
    const loading = ref(false)
    const error = ref(null)
    const checkInterval = ref(null)
    
    // Refs
    const playerWrapper = ref(null)
    const videoContainer = ref(null)
    let player = null
    
    // Ëß¶Êë∏ÊâãÂäøÁÆ°ÁêÜÂô®
    const gestureManager = ref(null)
    
    // ÂêåÊ≠• initialIndex ÂèòÂåñ
    watch(() => props.initialIndex, (newVal) => {
      currentIndex.value = newVal
    })
    
    // ÁõëÂê¨ show Â±ûÊÄßÂèòÂåñÔºåÂΩìÊâìÂºÄÊó∂ÂàùÂßãÂåñËßÜÈ¢ëÊí≠ÊîæÂô®
    watch(() => props.show, async (newVal) => {
      if (newVal) {
        await nextTick()
        initPlayer()
        
        // ÂàùÂßãÂåñÊâãÂäøÁÆ°ÁêÜÂô®
        setTimeout(() => {
          initGestureManager()
          gestureManager.value.activate()
        }, 100)
      } else {
        disposePlayer()
        
        // ÂÅúÁî®ÊâãÂäøÁÆ°ÁêÜÂô®
        if (gestureManager.value) {
          gestureManager.value.deactivate()
        }
      }
    })
    
    // ÂÖ≥Èó≠Êü•ÁúãÂô®
    const close = () => {
      // Ê∏ÖÁêÜËßÜÈ¢ëÊí≠ÊîæÂô®
      disposePlayer()
      
      // ÂÅúÁî®ÊâãÂäøÁÆ°ÁêÜÂô®
      if (gestureManager.value) {
        gestureManager.value.deactivate()
      }
      
      // ÈÄöÁü•Áà∂ÁªÑ‰ª∂ÂÖ≥Èó≠
      emit('close')
    }
    
    // ÊÄªËßÜÈ¢ëÊï∞Èáè
    const totalVideos = computed(() => props.videos ? props.videos.length : 0)
    
    // Ëé∑ÂèñÂΩìÂâçËßÜÈ¢ëÂØπË±°
    const currentVideo = computed(() => {
      if (props.videos && props.videos[currentIndex.value]) {
        return props.videos[currentIndex.value]
      }
      return null
    })
    
    // Ëé∑ÂèñËßÜÈ¢ëÁº©Áï•ÂõæURL
    const getThumbnailUrl = (fileName) => {
      return VideoService.getThumbnailUrl(props.eventId, fileName)
    }
    
    // ÂàùÂßãÂåñËßÜÈ¢ëÊí≠ÊîæÂô®
    const initPlayer = async () => {
      // Ê∏ÖÁêÜ‰πãÂâçÁöÑÊí≠ÊîæÂô®ÂÆû‰æã
      disposePlayer()
      
      // Ê£ÄÊü•ËßÜÈ¢ëÊï∞ÊçÆÊòØÂê¶Â≠òÂú®
      if (!currentVideo.value || !props.eventId || !playerWrapper.value) {
        logger.error('Missing video data or player element')
        setError('ËßÜÈ¢ëÊï∞ÊçÆÊàñÊí≠ÊîæÂô®ÂÖÉÁ¥†‰∏çÂèØÁî®')
        return
      }
      
      logger.info(`Initializing player for video: ${currentVideo.value.fileName}`)
      loading.value = true
      
      try {
        // Ëé∑ÂèñËßÜÈ¢ëURL
        const urlResult = await VideoService.getVideoUrl(
          props.eventId, 
          currentVideo.value.fileName
        )
        
        if (!urlResult.success) {
          if (urlResult.isProcessing) {
            setError('ËßÜÈ¢ëÊ≠£Âú®ËΩ¨Á†Å‰∏≠ÔºåËØ∑Á®çÂêéÂÜçËØï', true)
          } else {
            setError(urlResult.error)
          }
          return
        }
        
        // ÈÖçÁΩÆÊí≠ÊîæÂô®
        const playerConfig = {
          sources: [{
            src: urlResult.videoUrl,
            type: 'application/x-mpegURL' // HLS format
          }]
        }

        // ÂàõÂª∫ video ÂÖÉÁ¥†
        const videoElement = document.createElement('video')
        videoElement.id = 'video-player'
        videoElement.className = 'video-js vjs-big-play-centered'
        
        // Ê∏ÖÁ©∫ÂÆπÂô®Âπ∂Ê∑ªÂä†Êñ∞ÂàõÂª∫ÁöÑÂÖÉÁ¥†
        playerWrapper.value.innerHTML = ''
        playerWrapper.value.appendChild(videoElement)
        
        // ÂàùÂßãÂåñÊí≠ÊîæÂô®
        player = initializeVideoPlayer(
          videojs, 
          'video-player', 
          playerConfig,
          onPlayerReady,
          onPlayerError
        )
        
        // Ê∑ªÂä†È¢ùÂ§ñÁöÑ‰∫ã‰ª∂ÁõëÂê¨Âô®
        setupPlayerEventListeners()
      } catch (err) {
        logger.error(`Error initializing player: ${err.message}`)
        setError('ÂàùÂßãÂåñÊí≠ÊîæÂô®Â§±Ë¥•')
      } finally {
        loading.value = false
      }
    }
    
    // Ê∏ÖÁêÜÊí≠ÊîæÂô®
    const disposePlayer = () => {
      if (player) {
        try {
          player.dispose()
          player = null
        } catch (e) {
          logger.warning(`Error disposing player: ${e.message}`)
        }
      }
      
      // Ê∏ÖÈô§Ê£ÄÊü•ËΩ¨Á†ÅÁä∂ÊÄÅÁöÑÂÆöÊó∂Âô®
      if (checkInterval.value) {
        clearInterval(checkInterval.value)
        checkInterval.value = null
      }
    }
    
    // Player event callbacks
    const onPlayerReady = () => {
      logger.success('Player ready')
    }
    
    const onPlayerError = (err) => {
      const errorMessage = typeof err === 'string' ? err : (err?.message || JSON.stringify(err) || 'Êú™Áü•ÈîôËØØ')
      logger.error(`Player error: ${errorMessage}`)
      setError(`Êí≠ÊîæÂô®ÈîôËØØ: ${errorMessage}`)
    }
    
    // Setup additional player events
    const setupPlayerEventListeners = () => {
      if (!player) return
      
      player.on('loadstart', () => logger.info('ÂºÄÂßãÂä†ËΩΩËßÜÈ¢ë'))
      player.on('loadeddata', () => logger.success('ËßÜÈ¢ëÊï∞ÊçÆÂ∑≤Âä†ËΩΩ'))
      player.on('loadedmetadata', () => {
        logger.success('ËßÜÈ¢ëÂÖÉÊï∞ÊçÆÂ∑≤Âä†ËΩΩ')
        const duration = player.duration()
        logger.info(`ËßÜÈ¢ëÊó∂Èïø: ${duration}Áßí`)
      })
      
      player.on('play', () => logger.info('ËßÜÈ¢ëÂºÄÂßãÊí≠Êîæ'))
      player.on('pause', () => logger.info('ËßÜÈ¢ëÊöÇÂÅú'))
      player.on('ended', () => {
        logger.info('ËßÜÈ¢ëÊí≠ÊîæÁªìÊùü')
        // Auto-play next video if available
        if (currentIndex.value < totalVideos.value - 1) {
          nextVideo()
        }
      })
    }
    
    // ÂàáÊç¢Âà∞‰∏ä‰∏Ä‰∏™ËßÜÈ¢ë
    const prevVideo = async () => {
      if (currentIndex.value > 0) {
        currentIndex.value--
        await nextTick()
        initPlayer()
        // ÈÄöÁü•Áà∂ÁªÑ‰ª∂Á¥¢ÂºïÂèòÂåñ
        emit('indexChange', currentIndex.value)
        
        // ÈáçÊñ∞ÂàùÂßãÂåñÊâãÂäøÁÆ°ÁêÜÂô®
        if (gestureManager.value) {
          gestureManager.value.deactivate()
        }
        initGestureManager()
        gestureManager.value.activate()
      }
    }

    // ÂàáÊç¢Âà∞‰∏ã‰∏Ä‰∏™ËßÜÈ¢ë
    const nextVideo = async () => {
      if (currentIndex.value < props.videos.length - 1) {
        currentIndex.value++
        await nextTick()
        initPlayer()
        // ÈÄöÁü•Áà∂ÁªÑ‰ª∂Á¥¢ÂºïÂèòÂåñ
        emit('indexChange', currentIndex.value)
        
        // ÈáçÊñ∞ÂàùÂßãÂåñÊâãÂäøÁÆ°ÁêÜÂô®
        if (gestureManager.value) {
          gestureManager.value.deactivate()
        }
        initGestureManager()
        gestureManager.value.activate()
      }
    }

    // SnackbarÊòæÁ§∫ËæÖÂä©ÂáΩÊï∞
    const showSnackbar = (message, options = {}) => {
      snackbar.value.show = false;
      nextTick(() => {
        snackbar.value = {
          show: true,
          message,
          subtext: options.subtext || '',
          type: options.type || 'info',
          duration: options.duration || 4000
        };
        logger.info('ÊòæÁ§∫Snackbar:', snackbar.value);
      });
    }

    // ÈîôËØØÂ§ÑÁêÜ
    const setError = (message, isProcessing = false) => {
      error.value = {
        message,
        isProcessing
      }
      
      if (isProcessing) {
        logger.warning(`Processing error: ${message}`)
        // SnackbarÊèêÁ§∫ËΩ¨Á†ÅÊú™ÂÆåÊàê
        showSnackbar('ËßÜÈ¢ëÊ≠£Âú®ËΩ¨Á†Å‰∏≠ÔºåËØ∑Á®çÂêéÂÜçËØï', {
          subtext: message,
          type: 'warning',
          duration: 3000
        })
      } else {
        logger.error(`Error: ${message}`)
        // SnackbarÊèêÁ§∫ÈîôËØØ‰ø°ÊÅØ
        showSnackbar(`ÈîôËØØ: ${message}`, {
          type: 'error',
          duration: 3000
        })
      }
      // 3ÁßíÂêéËá™Âä®ÂÖ≥Èó≠ÂºπÁ™ó
      setTimeout(() => {
        close()
      }, 3000)
    }
    
    
    // ÁßªÈô§Ëá™Âä®Ê£ÄÊü•ËΩ¨Á†ÅÈÄªËæë

    // ÈîÆÁõò‰∫ã‰ª∂Â§ÑÁêÜ
    const handleKeyDown = (e) => {
      if (!props.show) return
      
      switch (e.key) {
        case 'ArrowLeft':
          e.preventDefault()
          prevVideo()
          break
        case 'ArrowRight':
          e.preventDefault()
          nextVideo()
          break
        case 'Escape':
          e.preventDefault()
          close()
          break
      }
    }

    onMounted(() => {
      // Ê∑ªÂä†ÈîÆÁõò‰∫ã‰ª∂ÁõëÂê¨
      document.addEventListener('keydown', handleKeyDown)
    })

    // ÂàùÂßãÂåñÊâãÂäøÁÆ°ÁêÜÂô®
    const initGestureManager = () => {
      if (gestureManager.value) {
        gestureManager.value.deactivate()
      }
      
      gestureManager.value = createPhotoViewerGesture({
        minScale: 1,  // ËßÜÈ¢ë‰∏çÈúÄË¶ÅÁº©ÊîæÂäüËÉΩ
        maxScale: 1,
        swipeThreshold: 50,
        maxVerticalDistance: 100
      })
      
      // ËÆæÁΩÆÊâãÂäøÂõûË∞É
      gestureManager.value.on('swipeLeft', () => {
        nextVideo()
      })
      
      gestureManager.value.on('swipeRight', () => {
        prevVideo()
      })
    }

    // Ëß¶Êë∏‰∫ã‰ª∂‰ª£ÁêÜÂáΩÊï∞
    const handleTouchStart = (e) => {
      if (!props.show || !gestureManager.value) return
      gestureManager.value.handleTouchStart(e)
    }

    const handleTouchMove = (e) => {
      if (!props.show || !gestureManager.value) return
      gestureManager.value.handleTouchMove(e)
    }

    const handleTouchEnd = (e) => {
      if (!props.show || !gestureManager.value) return
      gestureManager.value.handleTouchEnd(e)
    }

    // ÁªÑ‰ª∂Âç∏ËΩΩÊó∂ÁßªÈô§‰∫ã‰ª∂ÁõëÂê¨
    onBeforeUnmount(() => {
      document.removeEventListener('keydown', handleKeyDown)
      // Ê∏ÖÁêÜÊí≠ÊîæÂô®
      disposePlayer()
      
      // ÂÅúÁî®ÊâãÂäøÁÆ°ÁêÜÂô®
      if (gestureManager.value) {
        gestureManager.value.deactivate()
      }
    })

    return {
      currentIndex,
      currentVideo,
      totalVideos,
      loading,
      error,
      playerWrapper,
      videoContainer,
      close,
      prevVideo,
      nextVideo,
      getThumbnailUrl,
      formatDuration,
      handleTouchStart,
      handleTouchMove,
      handleTouchEnd,
      snackbar,
      showSnackbar
    }
  }
}
</script>

<style scoped>
/* Ê®°ÊÄÅÊ°ÜÈÄöÁî®Ê†∑Âºè */
.video-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
}

.modal-content {
  position: relative;
  z-index: 1001;
  max-width: 100vw;
  max-height: 100vh;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
}

/* ËßÜÈ¢ëÂÆπÂô®Ê†∑Âºè */
.large-video-container {
  width: 80vw;
  max-width: 1200px;
  height: 80vh;
  max-height: 90vh;
  overflow: hidden;
  border-radius: 15px;
  position: relative;
  margin-bottom: 15px;
  background: rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
}

.video-player-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.video-player-container > div {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* VideoJS Ê†∑ÂºèË¶ÜÁõñ */
:deep(.video-js) {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  overflow: hidden;
}

:deep(.vjs-big-play-centered .vjs-big-play-button) {
  margin-top: -1.5em;
  margin-left: -1.5em;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(0, 0, 0, 0.5);
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  padding: 10px;
  z-index: 1010;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.video-counter {
  color: white;
  font-size: 16px;
  margin: 0;
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  text-align: center;
}

.swipe-hint {
  display: block;
  position: absolute;
  top: 10px;
  left: 0;
  right: 0;
  text-align: center;
}

.hint-text {
  color: white;
  font-size: 12px;
  background: rgba(0, 0, 0, 0.5);
  padding: 4px 12px;
  border-radius: 15px;
  display: inline-block;
  font-size: 11px;
  opacity: 0.8;
  animation: fadeInOut 3s ease-in-out infinite;
}

/* ËßÜÈ¢ë‰ø°ÊÅØ */
.video-info {
  position: absolute;
  top: 40px; /* Ë∞ÉÊï¥È°∂ÈÉ®‰ΩçÁΩÆÔºåÈÅøÂÖç‰∏é swipe-hint ÈáçÂè† */
  left: 0;
  right: 0;
  text-align: center;
  padding: 10px;
  z-index: 1002;
}

.video-title {
  color: white;
  font-size: 18px;
  margin: 0;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.5);
  background-color: rgba(0, 0, 0, 0.5);
  display: inline-block;
  padding: 5px 15px;
  border-radius: 20px;
}

/* ÂØºËà™ÊåâÈíÆ */
.nav-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1001;
  background: rgba(0, 0, 0, 0.7);
  border: 2px solid rgba(255, 255, 255, 0.3);
  color: white;
  font-size: 24px;
  padding: 12px 8px;
  min-width: 40px;
  min-height: 40px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-btn.prev {
  left: 10px;
}

.nav-btn.next {
  right: 10px;
}

.nav-btn:hover,
.nav-btn:active {
  background: rgba(0, 0, 0, 0.8);
  border-color: rgba(255, 255, 255, 0.5);
}

.nav-btn:active {
  transform: translateY(-50%) scale(0.95);
}

/* Âä†ËΩΩÂíåÈîôËØØÁä∂ÊÄÅ */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1005;
}

.loading-content {
  text-align: center;
  color: white;
}

.loading-spinner {
  font-size: 40px;
  margin-bottom: 15px;
  animation: spin 1.5s linear infinite;
}

.error-notification {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(255, 0, 0, 0.8);
  color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  max-width: 300px;
  z-index: 1006;
}

.error-notification.transcoding {
  background: rgba(255, 165, 0, 0.8);
}

.error-icon {
  font-size: 30px;
  margin-bottom: 10px;
}

.retry-btn, .close-error-btn {
  margin-top: 15px;
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  background: white;
  color: black;
  cursor: pointer;
  margin-right: 10px;
}

/* Âä®ÁîªÂÆö‰πâ */
@keyframes fadeInOut {
  0%, 100% { opacity: 0.4; }
  50% { opacity: 1; }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* ÂìçÂ∫îÂºèËÆæËÆ° */
@media (max-width: 768px) {
  .large-video-container {
    width: 95vw;
    height: 85vh;
    margin-bottom: 0;
    border-radius: 0;
  }
  
  :deep(.video-js) {
    border-radius: 0;
  }
  
  .nav-btn {
    font-size: 30px;
    padding: 15px 10px;
  }
}

/* Êõ¥Â∞èÂ±èÂπïËÆæÂ§áÁöÑ‰ºòÂåñ */
@media (max-width: 480px) {
  .modal-content .nav-btn {
    font-size: 20px;
    padding: 10px 6px;
    min-width: 36px;
    min-height: 36px;
  }
  
  .modal-content .nav-btn.prev {
    left: 5px;
  }

  .modal-content .nav-btn.next {
    right: 5px;
  }
  
  .large-video-container {
    width: 95vw;
    /* ‰ΩøÁî® calc ÈÅøÂÖçÂ∫ïÈÉ®ÂØºËà™Ê†èÈÅÆÊå° */
    height: calc(100vh - 60px);
    margin-top: -20px; /* Ë∞ÉÊï¥ÂûÇÁõ¥‰ΩçÁΩÆ */
  }
  
  .modal-close {
    top: 10px;
    right: 10px;
    font-size: 20px;
